@import url(https://fonts.googleapis.com/css2?family=Quicksand:wght@300;400;500;600;700&display=swap);
* {
  margin: 0;
  font-family: "Quicksand";
  color: #333;
}
.navbar {
  padding: 20px;
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  margin: 0 auto;
  border-bottom: 1px solid #f2f2f2;
  background-color: #fcfcfc;
  padding-left: 100px;
}
.navbar h1 {
  color: #65d5f1;
}
.navbar .links {
  margin-left: auto;
  padding-right: 100px;
}
.navbar a {
  margin-left: 20px;
  text-decoration: none;
  padding: 6px;
}
.navbar a:hover {
  color: #65d5f1;
}
.content {
  margin: 20px auto;
}
.body {
  margin: 20px auto;
}
.header {
  text-align: center;
  padding-bottom: 25px;
}
p {
  text-align: center;
  padding-bottom: 2px;
}
.p-text {
  padding-left: 200px;
  padding-right: 200px;
}
.subtitle {
  font-weight: 600;
}
.home {
  border: 1px solid green;
  overflow: hidden;
}
.home-container {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
}
.home-title {
  padding-top: 3%;
  padding-bottom: 4%;
  font-size: 200px;
  color: #04cdff;
  white-space: nowrap;
  overflow: hidden;
}
.home-steps {
  width: 600px;
  float: left; /* add this */
  border: 1px solid red;
}

.home-title {
  -webkit-animation: animated-text 3s steps(20, end) 1s 1 normal both;
          animation: animated-text 3s steps(20, end) 1s 1 normal both;
}

.home-subtitle {
  text-align: center;
  padding-bottom: 5%;
}

.home-wrapper {
  padding: 5%;
  background: #65d5f1;
  width: 100%;
  height: 200px;
}

@-webkit-keyframes animated-text {
  from {
    width: 0;
  }
  to {
    width: 75%;
  }
}

@keyframes animated-text {
  from {
    width: 0;
  }
  to {
    width: 75%;
  }
}

#element {
  -webkit-animation: 8s ease 0s normal forwards 1 fadein;
  animation: 8s ease 0s normal forwards 1 fadein;
}

#element-subtitle {
  -webkit-animation: 10s ease 0s normal forwards 1 fadein;
  animation: 10s ease 0s normal forwards 1 fadein;
}

@keyframes fadein {
  0% {
    opacity: 0;
  }
  66% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}

@-webkit-keyframes fadein {
  0% {
    opacity: 0;
  }
  66% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}

.card {
  display: -webkit-flex;
  display: flex;
  /* justify-content: space-evenly; */
  width: 86%;
  height: 400px;
  padding: 25px;
  border-radius: 20px;

  /* color: white; */

  background-image: linear-gradient(25deg, #ff5f6d, #ffc371);
}

.button-container {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  height: 400px;
  -webkit-flex: 1 1;
          flex: 1 1;
}
.text-container {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  /* flex: 1; */
  width: 45%;
}
.question {
  text-align: center;
  font-size: 25px;
  padding-bottom: -10%;
}
.button {
  height: 10%;
  font-size: 18px;
  font-weight: 600;
  color: #ffffff;
  background-color: #84e2ee;
  margin-bottom: 5px;
  border-radius: 10px;
  /* display: flex;
  justify-content: center;
  align-items: center; */
  border: 2px solid #65d5f1;
  cursor: pointer;
}
.button:hover {
  background-color: #29c9df;
}
.scorebutton {
  height: 10%;
  font-size: 18px;
  font-weight: 600;
  color: #ffffff;
  background-color: #84e2ee;
  border-radius: 10px;
  display: -webkit-flex;
  display: flex;
  margin-top: 10%;
  padding-left: 3%;
  padding-right: 3%;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  border: 2px solid #65d5f1;
  cursor: pointer;
}
.resetbutton {
  height: 10%;
  font-size: 18px;
  font-weight: 600;
  color: #ffffff;
  background-color: #84e2ee;
  border-radius: 10px;
  display: -webkit-flex;
  display: flex;
  margin-top: 3%;
  padding-left: 3%;
  padding-right: 3%;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  border: 2px solid #65d5f1;
  cursor: pointer;
}
.scorebutton:hover {
  background-color: #95ff57;
}
.resetbutton:hover {
  background-color: #ff68a2;
}

.dashboard {
  display: -webkit-flex;
  display: flex;
  /* justify-content: space-evenly; */
  -webkit-flex-direction: column;
          flex-direction: column;
  width: 86%;
  height: 100%;
  padding: 25px;
  border-radius: 20px;

  /* color: white; */
  /* border: 2px solid #65d5f1; */

  background: #a8eeff;
}
.text {
  -webkit-justify-content: center;
          justify-content: center;
}
.container1 {
  width: 100%;
  background-color: #ffffff;
  border-radius: 25px;
}
.container-locked {
  width: 100%;
  background-color: #eeeeee;
  border-radius: 25px;
}
.skills {
  text-align: right;
  padding-right: 20px;
  padding: 1%;
  line-height: 20px;
  color: white;
  border-radius: 25px;
}
p {
  padding-top: 0.75%;
  padding-bottom: 0.75%;
  font-weight: 450;
}
h2 {
  text-align: center;
}
.bar1 {
  background-color: #f85c51;
  transition: 0.4s linear;
}
.bar2 {
  background-color: #f8c059;
  transition: 0.4s linear;
}
.bar3 {
  background-color: #f7f463;
  transition: 0.4s linear;
}
.bar4 {
  background-color: #6bf177;
  transition: 0.4s linear;
}
.bar5 {
  background-color: #55e2ec;
  transition: 0.4s linear;
}
.bar6 {
  background-color: #cebcff;
  transition: 0.4s linear;
}
.locked {
  width: 4%;
}
.progress {
  transition: 1s cubic-bezier(0.5, 0.1, 0.25, 1);
  transition-delay: 0.35s;
}

.suggestions {
  margin: auto;
  width: 88%;
  padding: 8px;
  border-radius: 20px;
  border: 5px solid #55e2ec;
  font-size: 20px;
  margin-bottom: 20px;

  /* color: white; */
  /* border: 2px solid #65d5f1; */
}

.suggestions:hover {
  background-color: #55e2ec;
}

